@import url("https://fonts.googleapis.com/css2?family=Arimo:ital,wght@0,400..700;1,400..700&display=swap");
.costumCurser {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  position: absolute;
  transition: transform 0.2s ease-in-out;
  transform-origin: center;
  pointer-events: none;
  z-index: 99;
}

nav {
