  justify-content: center;
  padding: 1em;
  padding-top: 2em;
  position: absolute;
  top: 0;
}
nav .innerNav {
  width: 100%;
  max-width: 1400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav .innerNav .Logo {
  color: white;
