  font-weight: 600;
  transition: font-size 0.3s ease;
}

.burgerMenu {
  font-size: 2.2em;
  color: white;
}

.mouse {
  width: 32px;
  height: 72px;
  border: 3px solid rgba(255, 255, 255, 0.66);
  border-radius: 60px;
  position: absolute;
  bottom: 1em;
}
.mouse::before {
  content: "";
  width: 8px;
  height: 8px;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(255, 255, 255, 0.66);
  border-radius: 50%;
  opacity: 1;
  animation: wheel 2s infinite;
  -webkit-animation: wheel 2s infinite;
}

@keyframes wheel {
  to {
    opacity: 0;
    top: 60px;
  }
}
.Hero {
